import {hostUrl} from '@/http/request.js';
export default{
	props:{
		list:{
			type:Array,
			default:() => {
				return []
			}
		}
	},
	data(){
		return {
			hostUrl:hostUrl
		}
	},
	methods:{
		goDetail(id){
			uni.navigateTo({
				url:`/pages/details/details?id=${id}`
